.dash_title_container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 80px;
  margin-bottom : 20px;
}

.dash_title {
  font-weight: 400;
  font-size: 46px;
  margin-left: 4.1667vw;
  margin-top: 20px;
  margin-bottom: 20px;
}


.top_vstack{
  display:flex;
  flex-direction: column;
  justify-content: flex-start;
  width : 100%;
  margin-bottom: 100px;
}
 .pane_container{
   width: 91.67vw;
   margin: 0px 4.1667vw;
   display: flex;
   flex-direction: row;
   justify-content: space-between;
 }

 .tile_container_full{
   display: flex;
   flex-direction: column;
   justify-content: space-between;
 }

 .tile_container_common{
   width: 92%;
 }

 .tile_container_half{
   width: 48%;
   display: flex;
   flex-direction: column;
   justify-content: space-between;
 }

 .tile_container_hstack{
   display: flex;
   flex-direction: row;
   justify-content: space-between;
   align-items: stretch;
 }

 .tile_header{
   font-size: 18px;
   font-weight: 500;
   margin-top: 20px;
   margin-bottom: 15px;
 }
 .pane{
   width: 44.4vw;
   min-height: 1100px;
   border-radius: 10px;
   display: flex;
   flex-direction: column;
   justify-content: flex-start;
   align-items: center;
 }
 .tile{
   display: flex;
   flex-direction: row;
   width: 100%;
   min-height: 350px;
   flex-grow: 1;
 }
 .slider_container {
  width: 80%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: relative;
 }

 .slider_input {
   position: absolute;
   left: calc(85% + 5px);
   font-family: inherit;
   width: 16%;
   font-size: 14px;
   font-weight: 500;
   color:  white;
   border: 0px;
   border-radius: 5px;
   height: 26px;
   text-align: center;
 }
 .slider_input:focus{
  outline: 0px;
}


.ping_form_label{
   font-size: 16px;
   font-weight: 500;
   margin-top: 10px;
   margin-bottom: 10px;
}
 .ping_form_container{
   width: 100%;
   height: 100%;
   display: flex;
   flex-direction: column;
   justify-content: space-between;
   align-items: center;
 }


.ping_submit {
  margin-top: 20px;
  margin-bottom: 25px;
  font-family: inherit;
  font-weight: 500;
  width: 128px;
  height: 36px;
  font-size: 18px;
  text-align: center;
  border: 0px;
  border-radius : 14px;
  color: white;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}
.at_a_glance_container{
display: flex;
flex-direction: column;
width: 100%;
align-items: center;
justify-content: space-evenly;
flex-grow: 1;
-webkit-transform-origin: center;
        transform-origin: center;
-webkit-transform: scale(1.15);
        transform: scale(1.15);
}

.at_a_glance_grade{
 font-size: 36px;
 margin : 0px;
 font-weight: 500;

}
.arc_bar_container{
  position: relative;
  height: 78px;
  width: 144px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}

.arc_bar{
  position: absolute;
}

.arc_bar_bg{
  /* stroke: var(--bg1); */
}
.arc_bar_fg{
