.arc_bar_label_top{
font-size: 24px;
font-family: inherit;
margin-top: 0px;
margin-bottom: 0px;
font-weight: 500;
}

.arc_bar_label_bottom{
font-size: 14px;
font-family: inherit;
font-weight: 500;
margin-top: 0px;
margin-bottom: 0px;
}

.arc_bar_tick{
  font-size: 10px;
  position: absolute;
  bottom: 0px;
  margin: 0px;
}

.arc_bar_tick_left{
  right:calc(100% + 3px)
}

.arc_bar_tick_right{
  left:calc(100% + 3px)
}
.monitor_tab_button_array{
  display: flex;
  flex-direction: row;
  align-self: stretch;
  justify-content: space-evenly;
  margin-top: 40px;
  margin-bottom: 25px;
}

.monitor_tab_button {
  background-color: var(--bg0);
  opacity: 0.4;
  border: 0px;
  width: 150px;
  height : 40px;
  border-radius: 10px;
  cursor: pointer;
}
.monitor_tab_button:hover{
  opacity: 0.6;
}


.flex_table_row{
  display: flex;
  flex-direction: row;
  align-items: center;
}

.flex_table_datum:last-child{
  margin-right: 20px;
}


.flex_table_datum{
text-align: center;  
display: flex;
flex-direction: row;
justify-content: center;
flex-basis: 0;
font-weight: 500;
}
.checkbox_bg {
  width: 25px;  
  height: 25px;  
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.checkbox_fg {
  width: 13px;  
  height: 13px;  
  border-radius: 1px;
}
.settings_input:focus{
  outline: 0px;
}
